<template>
  <el-select v-model="currentCityCode" filterable placeholder="全国地铁城市列表">
    <el-option
      v-for="item in cityList"
      :label="item.cityname"
      :key="item.adcode"
      :value="item.adcode"
    >{{ item.cityname }} ({{ item.spell }})</el-option>
  </el-select>
  <div id="mapContainer">
    <div id="mysubway"></div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data: function () {
    return {
      cityList: [],
      currentCityCode: '',//北京的adcode       <!-- :label="item.cityname" --> [{ "adcode": "1100", "spell": "beijing", "cityname": "北京" }, { "adcode": "3100", "spell": "shanghai", "cityname": "上海" }, { "adcode": "4401", "spell": "guangzhou", "cityname": "广州" }, { "adcode": "4403", "spell": "shenzhen", "cityname": "深圳" }, { "adcode": "4201", "spell": "wuhan", "cityname": "武汉" }, { "adcode": "1200", "spell": "tianjin", "cityname": "天津" }, { "adcode": "3201", "spell": "nanjing", "cityname": "南京" }, { "adcode": "8100", "spell": "xianggang", "cityname": "香港" }, { "adcode": "8200", "spell": "aomen", "cityname": "澳门" }, { "adcode": "5000", "spell": "chongqing", "cityname": "重庆" }, { "adcode": "3301", "spell": "hangzhou", "cityname": "杭州" }, { "adcode": "3306", "spell": "shaoxing", "cityname": "绍兴" }, { "adcode": "2101", "spell": "shenyang", "cityname": "沈阳" }, { "adcode": "2102", "spell": "dalian", "cityname": "大连" }, { "adcode": "5101", "spell": "chengdu", "cityname": "成都" }, { "adcode": "2201", "spell": "changchun", "cityname": "长春" }, { "adcode": "3205", "spell": "suzhou", "cityname": "苏州" }, { "adcode": "4406", "spell": "foshan", "cityname": "佛山" }, { "adcode": "5301", "spell": "kunming", "cityname": "昆明" }, { "adcode": "6101", "spell": "xian", "cityname": "西安" }, { "adcode": "4101", "spell": "zhengzhou", "cityname": "郑州" }, { "adcode": "2301", "spell": "haerbin", "cityname": "哈尔滨" }, { "adcode": "4301", "spell": "changsha", "cityname": "长沙" }, { "adcode": "3302", "spell": "ningbo", "cityname": "宁波" }, { "adcode": "3202", "spell": "wuxi", "cityname": "无锡" }, { "adcode": "3702", "spell": "qingdao", "cityname": "青岛" }, { "adcode": "3601", "spell": "nanchang", "cityname": "南昌" }, { "adcode": "3501", "spell": "fuzhou", "cityname": "福州" }, { "adcode": "4419", "spell": "dongguan", "cityname": "东莞" }, { "adcode": "4501", "spell": "nanning", "cityname": "南宁" }, { "adcode": "3401", "spell": "hefei", "cityname": "合肥" }, { "adcode": "3402", "spell": "wuhu", "cityname": "芜湖" }, { "adcode": "1301", "spell": "shijiazhuang", "cityname": "石家庄" }, { "adcode": "5201", "spell": "guiyang", "cityname": "贵阳" }, { "adcode": "3502", "spell": "xiamen", "cityname": "厦门" }, { "adcode": "6501", "spell": "wulumuqi", "cityname": "乌鲁木齐" }, { "adcode": "3303", "spell": "wenzhou", "cityname": "温州" }, { "adcode": "3701", "spell": "jinan", "cityname": "济南" }, { "adcode": "6201", "spell": "lanzhou", "cityname": "兰州" }, { "adcode": "3204", "spell": "changzhou", "cityname": "常州" }, { "adcode": "3203", "spell": "xuzhou", "cityname": "徐州" }, { "adcode": "1401", "spell": "taiyuan", "cityname": "太原" }, { "adcode": "4103", "spell": "luoyang", "cityname": "洛阳" }, { "adcode": "1501", "spell": "huhehaote", "cityname": "呼和浩特" }]
      subwayObj: {}  //subway对象
    }
  },
  mounted: function () {
    //console.log(window);
    var that = this;
    var checkJsLoadInterval = setInterval(function () {
      if (typeof window.subway != 'undefined') {
        clearInterval(checkJsLoadInterval);
        that.mapInit(window);
      }
    }, 50);

    //const { proxy } = getCurrentInstance();
    // that.$axios.get('https://restapi.amap.com/v3/config/getCityList?key=a05a8f8fab0d109b825bbf4b21c20fad')
    //   .then((response) => {
    //     console.log(response)
    //   })

  },
  methods: {
    mapInit: function () {
      var that = this;
      that.subwayObj = window.subway("mysubway", {
        adcode: that.currentCityCode || '1100',
        theme: 'colorful',
        easy: 1
      });

      that.subwayObj.getCityList(function (response) {
        for (var key in response) {
          that.cityList.push({
            "adcode": key,
            "spell": response[key]['city'],
            "cityname": response[key]['name']
          });
        }
      });

      //点击站点，显示此站点的信息窗体
      that.subwayObj.event.on("station.touch", function (ev, info) {
        console.log("station.touch");
        var id = info.id;
        that.subwayObj.stopAnimation();
        that.subwayObj.addInfoWindow(id, {});
        var center = that.subwayObj.getStCenter(id);
        that.subwayObj.setCenter(center);
      });

      //设置起点
      that.subwayObj.event.on("startStation.touch", function (ev, info) {
        that.subwayObj.stopAnimation();
        that.subwayObj.clearInfoWindow();
        that.subwayObj.setStart(info.id, {});
        startInfo = info;
        route();
      });

      //设置终点
      that.subwayObj.event.on("endStation.touch", function (ev, info) {
        that.subwayObj.stopAnimation();
        that.subwayObj.clearInfoWindow();
        that.subwayObj.setEnd(info.id, {});
        endInfo = info;
        route();
      });

      //路线规划
      var startInfo = {},
        endInfo = {};
      function route() {
        if (startInfo.id && endInfo.id) {
          that.subwayObj.route(startInfo.id, endInfo.id, {});
          startInfo = {};
          endInfo = {};
        }
      }
    }
  },
  watch: {
    currentCityCode: function () {
      // 切换城市
      this.subwayObj.setAdcode(this.currentCityCode);
    }
  }
}
</script>

